@import url(https://fonts.googleapis.com/css?family=Courgette|Open+Sans);
body {
    position: relative;
    background-color: #01579b;
    font-family: "Helvetica Neue", Helvetica;
}

button {
    font-style: normal;
    font-weight: bold;
    line-height: normal;
}

.center-block {
    margin: 0 auto;
    float: none;
}

/** Global Variables */
:root {
    --main-blue: #133d5e;
    --accent-blue: #01579b;
}

/** Fonts */


.login-container {
    border-radius: 0.5rem;
    margin: 10rem auto;
    float: none;
    width: 90%;
    max-width: 30rem;
    padding: 3rem 0rem;
    overflow: hidden;
    background: rgba(229, 229, 229, 0.9);
    box-shadow: 0 0.25rem 0.25rem rgba(0, 0, 0, 0.25);
}

.login-container .title {
    color: var(--main-blue);
    font-family: "Courgette";
    line-height: normal;
    font-size: 3.5rem;
    text-shadow: 0.15rem 0.15rem 0.3rem rgba(0, 0, 0, 0.15);
}

.login-btn {
    border-radius: 0.5rem;
    overflow: hidden;
    display: block;
    width: 90%;
    margin: auto;
    margin-top: 2rem;
    background: var(--accent-blue);
    border-color: var(--accent-blue);
    box-shadow: 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.25);
}

.login-btn h6 {
    padding: 0.75rem 0.5rem;
    font-size: 1rem;
    margin: auto;
    font-weight: 400;
    color: white;
    width: 100%;
}

